
html{
    font-size: 62.5%;
}
body{
    font-size:1.6rem;/* 16px*/
    line-height: 2;
    margin: 0px;
    background-image: url("../../lightVeneer.png");
    font-family: Lato, "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}
h1 {
    font-size: 36px;/* IE8以下とAndroid4.3以下用フォールバック */
    font-size: calc(1.5rem + ((1vw - 4.8px) * 10.5556)); /*36-100px*/
    line-height: 1.3;
}
h2 {
    font-size: 24px;/* IE8以下とAndroid4.3以下用フォールバック */
    font-size: calc(1.35rem + ((1vw - 4.8px) * 5.3333));
    line-height: 1.3;
}
p{
  font-size:1.6rem;
  font-size: calc(1rem + ((1vw - 6.4px) * 1.0714));
}
@media screen and (min-width: 1200px) {/* 1200px以上*/
h1 {
      font-size: 5rem;/* 36px*/
      font-weight: lighter;
}
h2 {
        font-size: 6rem;
}
p{
      font-size: 16px;
}
.uk-navbar-nav>li>a{
