.scroll-nav {
      /* スクロールバーを見えなくするため */
      height: 48px; /* 実際に表示する高さ */
      overflow-y: hidden;
}

    /* 中身がスクロールするビュー */
.scroll-nav__view {
      -webkit-overflow-scrolling: touch;
      overflow-x: auto;
}

    /* 実際にスクロールするオブジェクト */
.scroll-nav__list {
      /* tableにすることで、横幅指定をすることなくmargin: autoによる中央寄せが可能になる */
      display: table;
      list-style: none;
      margin: 0 auto;
      /* 下方向に余分なpadding部分を持たせ、そこにスクロールバーが表示される */
      padding: 0 0 24px 0;
}
.scroll-nav__item {
      /* itemを横並びに、かつ親要素がその親要素の横幅からはみだすことが可能になる */
      display: table-cell;
}
.scroll-nav a {
      display: block;
      padding: 12px;
      /* 文字列の自動改行を禁止 */
      white-space: nowrap;
}

    /* 以下サンプル用デザイン */
.scroll-nav {
      background: rgb(50, 50, 50);
}
.scroll-nav a {
      color: rgb(224, 224, 224);
      font-family: sans-serif;
      font-size: 14px;
      /* 行高が24pxになるように調整 24/14 */
      line-height: 1.71428;
      text-decoration: none;
}
}
